$color-primary: #e1f5fe;
$color-primary-light: #ffffff;
$color-primary-dark: #afc2cb;
$color-primary-text: #000000;

$color-secondary: #01579b;
$color-secondary-light: #4f83cc;
$color-secondary-dark: #002f6c;
$color-secondary-text: #ffffff;

// background colors

@mixin bg($color-bg, $color) {
  background-color: $color-bg;
  color: $color; }

%bg-primary {
  @include bg($color-primary, $color-primary-text); }

%bg-primary-light {
  @include bg($color-primary-light, $color-primary-text); }

%bg-primary-dark {
  @include bg($color-primary-dark, $color-primary-text); }

%bg-secondary {
  @include bg($color-secondary, $color-secondary-text); }

%bg-secondary-light {
  @include bg($color-secondary-light, $color-secondary-text); }

%bg-secondary-dark {
  @include bg($color-secondary-dark, $color-secondary-text); }

// utils

%push-left {
  height: 100%;
  float: left; }

%pull-left {
  width: 100%;
  float: left; }
